<template>
    <el-container v-loading="loading">
        <div class="rise x add">
            <div class="title">{{title}}</div>
            <div class="content">
                <div class="item" v-for="(item, index) in data" :key="index">
                    <span>{{item.name}}</span>
                    <!-- input -->
                    <div v-if="!item.type">
                        <el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'count'" v-model="count" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'price'" v-model="price" :placeholder="item.placeholder"/>
                    </div>
                    <!-- select -->
                    <div v-if="item.type === 'select'">
                        <el-select v-if="item.module === 'school'" v-model="item.model" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.dValue"
                            ></el-option>
                        </el-select>
                        <el-select v-if="item.module === 'class'" v-model="item.model" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.dValue"
                            ></el-option>
                        </el-select>
                    </div>
                </div>
            </div>
            <div class="btn-list">
                <el-button @click="cancle()" class="btn">取消</el-button>
                <el-button @click="save()" class="btn">保存</el-button>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { getSchool, getClass } from 'data/listBody';
const titles = ["新建", "编辑"];
const defaultV = [
    { name:'学员姓名：', placeholder:'请输入学员姓名', module: 'name'},
    { name:'学员手机号：', placeholder:'请输入学员手机号', module: 'count'},
    { name:'校区：', placeholder:'请选择校区', module:'school', type:'select'},
    { name:'班级：', placeholder:'请选择班级', module:'class', type:'select'},
]

export default {
    name: "add",
    data() {
        return {
            title: "",
            id: "",
            data: defaultV,
            loading: false,            
            name:'',
            count:'',
            school:'',
            class:'',
        };
    },
    mounted() {
        this.dataInit();
        this.dropInit();
    },
    methods: {
        dropInit(){
            getSchool((res) => {
                let leftMenu = this.data.map(item => {
                    if(item.module === 'school') item.options = res;
                    return item;
                })
                this.data = leftMenu;
            })
            getClass((res) => {
                let leftMenu = this.data.map(item => {
                    if(item.module === 'class') item.options = res;
                    return item;
                })
                this.data = leftMenu;
            })
        },
        dataInit() {
            const { type, id } = this.$route.query;
            if (type !== "edit") return (this.title = titles[0]);
            this.title = titles[1];
            this.id = id;
            this.ajax();
        },
        ajax() {
            //查看详情
            // this.loading = true;
            // const param = queryString.stringify(params || {});
            // let url = urls[code];
            // this.api.get(`/role`).then((res)=>{
            // this.loading = false;
            // this.data = res.data.list;
            // this.setPage(res.data)
            // this.dataInit();
            // }).catch((err) => {
            //   console.log(err)
            //   this.$message(err.message)
            // })
        },
        save() {
            //保存详情
        },
        cancle() {
            //取消
            this.$router.go(-1);
        }
    }
};
</script>
 
